










import {
  defineComponent, reactive, computed, ref, toRefs,
} from '@vue/composition-api';

export default defineComponent({
  name: 'meeting-icon',
  components: {},
  props: {
    width: {
      type: Number,
      default: 10,
    },
  },
  setup(props, { refs, emit, root }) {
    const state = reactive({
      wrapStyle: {
        width: `${props.width}px`,
      },
    });
    return {
      ...toRefs(state),
    };
  },
});
