interface MeetUser{
  userId:string,
  roleId:string,
  owner:boolean
}
type MeetUsersType = MeetUser[];


const fetchMeetUsers = async (params:{cooperationId:string}) => {
  const res = (await axios({
    url: '/meeting/member/page',
    params: {
      pageNumber: 1,
      pageSize: 10000,
      ...params,
    },
  }))?.data || {};
  return res;
};

