







import {
  defineComponent, ref,
} from '@vue/composition-api';
import dayjs, { ConfigType } from 'dayjs';
import { DatePicker } from 'element-ui/types';

type Picker = {
  [k: string]: any
};

type Limit = 'after-now' | 'before-now';

type LimitOptions = {
  [T in Limit]: any
};


const getCheckedTime = (date: Date) => {
  const now = dayjs();
  const defaultTime = now.date() === dayjs(date).date() ? now.format('HH:mm:ss') : '';
  return defaultTime;
};

const formatedValue = (date: ConfigType, format: string) => {
  const valueFormat = format.replace(/[y|d]/g, code => code.toUpperCase()) || 'timestamp';
  if (valueFormat === 'timestamp') {
    return dayjs(date).valueOf();
  }
  return dayjs(date).format(valueFormat);
};


export default defineComponent({
  name: 'date-picker',
  inheritAttrs: false,
  props: {
    limit: {
      type: String, // 'after-now' | 'before-now'
    },
  },
  setup(props, { root, emit, attrs }) {
    const datePicker = ref<DatePicker & { picker: Picker }>();

    const limitOptions: LimitOptions = (() => {
      const getLimitValue = (date: ConfigType, type: 'min' | 'max' = 'max') => {
        date = dayjs(date).valueOf();
        const now = Date.now();
        return formatedValue(Math[type](date, now), attrs['value-format']);
      };
      const setRangeTime = ({ minDate, maxDate }: { minDate: Date, maxDate: Date }) => {
        const rangeTime = [getCheckedTime(minDate), getCheckedTime(maxDate)];
        const { picker } = datePicker.value!;
        setTimeout(() => {
          // el-element本身设置了10MS的settimeout,这里需要调用el-date-picker->picker->handleTimeChange方法。需维护
          picker.handleTimeChange(rangeTime[0], 'min');
          picker.handleTimeChange(rangeTime[1], 'max');
        }, 20);
      };

      return {
        'before-now': {
          disabledDate(time: ConfigType) {
            const nowStamp = dayjs().add(-1, 'day').valueOf();
            const timeStamp = dayjs(time).valueOf();
            return timeStamp < nowStamp;
          },
          setRangeTime,
          getLimitValue(value: Date[]) {
            return [getLimitValue(value[0]), getLimitValue(value[1])];
          },
        },
        'after-now': {
          disabledDate(time: ConfigType) {
            const nowStamp = dayjs().valueOf();
            const timeStamp = dayjs(time).valueOf();
            return timeStamp > nowStamp;
          },
          setRangeTime,
          getLimitValue(value: Date[]) {
            return [getLimitValue(value[0], 'min'), getLimitValue(value[1], 'min')];
          },
        },
      };
    })();

    const { type } = attrs;
    const limit = props.limit as Limit;
    const limitOption = limitOptions[limit];

    // 点击确定后：设置的时间超出限制时，需重新设置值(只针对timerange)
    const ensure = (value: Date | Date[]) => {
      if (attrs.type !== 'datetimerange') return;
      if (!Array.isArray(value)) return;
      const limitValue: (string | number)[] = limitOption?.getLimitValue(value);
      if (!limitValue) return;
      emit('input', limitValue);
    };


    const getPickerOptions = () => {
      let pickOptions;
      switch (type) {
        case 'datetimerange':
          pickOptions = {
            onPick({ minDate, maxDate }: { minDate: Date, maxDate: Date }) {
              if (!minDate || !maxDate) return;
              limitOption?.setRangeTime({ minDate, maxDate });
            },
          };
          break;
        default:
          break;
      }

      return {
        disabledDate: limitOption?.disabledDate,
        ...pickOptions,

      };
    };

    const pickerOptions = ref(getPickerOptions());


    const change = (value: Date | Date[]) => {
      ensure(value);
    };

    return {
      pickerOptions,
      datePicker,
      change,
    };
  },
});
