
























































































































































































import {
  defineComponent, reactive, computed, ref, toRefs, onMounted, markRaw, PropType, watch,
} from '@vue/composition-api';
import uploadToOSS from '@/utils/oss/uploadToOSS';
import axios from '@/utils/axios';
import DatePicker from '@/components/form/DatePicker.vue';
import BaseImage from '@/components/base/Image';
import OverText from '@/components/OverText.vue';
import i18n from 'i18next';
import MemberAdd from '@/views/file-list/components/workspace/MemberAdd.vue';
import User from '@/models/User';
import filterThumb from '@/filters/thumb';
import { getRelativePathFromUrl } from '@/utils/normalize-component';
import keys from '_/keys';
import difference from '_/difference';

import MemberSelector from '@/views/file-list/components/workspace/MemberSelector.vue';
import MemberSearcher from '@/views/file-list/components/workspace/MemberSearcher.vue';
import validator2ElemRule from '@/utils/validators/utils/validator-to-elem-rule';
import isFilled from '@/utils/validators/is-filled';
import { validateForm } from '@/utils/validators/validate-form';
import { ElForm } from 'element-ui/types/form';
import envs from '@/utils/envs';
import isDataPipeUrl from '@/utils/is-data-pipe-url';
import { Meet, MeetList } from '../index.vue';
import MeetupAccessPassGenerator from './MeetupAccessPassGenerator.vue';
import MemberInvitation from './MemberInvitation.vue';
import ImagePicker from './ImagePicker.vue';
import MeetUsers from './MeetUsers.vue';
import NoticeDialog from './NoticeDialog.vue';


export type MeetForm = Pick<Meet, 'name' | 'themeImage'> & { id?: string, dates: number[], isInform: 0 | 1, introduce: string };
export type OperateParams = Partial<Omit<MeetForm, 'dates'> & { startTime: number, endTime: number, mailStatus:boolean }>;

const operateMeeting = async (params: OperateParams) => {
  const url = params.id ? '/meeting/meeting/update' : '/meeting/meeting/add';
  const res = axios({
    url,
    method: 'post',
    data: params,
  });
  return res;
};

function loadImage(url: string, { crossOrigin } : { crossOrigin?: string } = {}) {
  return new Promise<HTMLImageElement>((resolve, reject) => {
    const img = document.createElement('img');
    img.onload = () => {
      resolve(img);
    };
    if (crossOrigin) {
      img.crossOrigin = crossOrigin;
    }
    img.onerror = () => {
      reject(new Error(`load ${url} error`));
    };
    img.src = url;
  });
}

async function urlToBlob(url: string, { withCredentials = envs.VUE_APP_IS_REMOTE && isDataPipeUrl(url) } = {}) {
  const img = await loadImage(url, { crossOrigin: withCredentials ? 'use-credentials' : 'anonymous' });
  return new Promise<Blob>((resolve, reject) => {
    const c = document.createElement('canvas');
    c.width = img.naturalWidth;
    c.height = img.naturalHeight;
    c.getContext('2d')!.drawImage(img, 0, 0, c.width, c.height);
    c.toBlob((result) => {
      if (result) {
        resolve(result);
      } else {
        reject(new Error('Convert blob error'));
      }
    }, 'image/png');
  });
}

export default defineComponent({
  name: 'operate-meeting',
  dialogify: {
    props: {
      width: '600px',
      flex: 'column',
      top: 'auto',
      bottom: 'auto',
    },
  },
  components: {
    ImagePicker, DatePicker, MeetUsers,
  },
  props: {
    item: {
      type: Object as () => Meet,
      default: () => ({}),
    },
  },
  setup(props, { refs, emit, root }) {
    const state = reactive({
      loading: false,
      themeImage: undefined as File | undefined,
      meetUsers: [],
      originMeetForm: markRaw({}) as MeetForm,
      meetForm: {
        id: '',
        name: '',
        dates: [],
        themeImage: '',
        isInform: 0,
        introduce: '',
      } as MeetForm,
      rules: {
        // name: [
        //   { required: true, message: '请输入会议名称', trigger: 'change' },
        // ],
        name: validator2ElemRule(isFilled),
        dates: validator2ElemRule(isFilled),
      },
      themeImages: [],
      themeImageLoading: false,
      diffInfo: {} as {id?:string, diff?:{[any:string]:any}},
    });
    const form = ref<ElForm>();
    const isEdit = computed(() => Object.keys(props.item).length);
    const hasDiffInfo = computed(() => !!Object.keys(state.diffInfo?.diff || []).length);

    const getItemDiff = () => {
      const isSame = (value: any, oldValue: any) => {
        if (typeof value === 'object') {
          return JSON.stringify(value) === JSON.stringify(oldValue);
        }
        return value === oldValue;
      };
      const diff = keys(state.meetForm).reduce((obj, key) => {
        const value = state.meetForm[key];
        const oldValue = state.originMeetForm[key];

        if (!isSame(value, oldValue)) {
          obj[key] = [value, oldValue];
        }
        return obj;
      }, {} as { [any: string]: any });
      return { id: state.meetForm.id, diff };
    };

    watch(() => state.meetForm, () => {
      console.log(state.meetForm);
      state.diffInfo = getItemDiff();
    }, { deep: true });

    const meetUsersRef = ref<InstanceType<typeof MeetUsers>>();

    const initMeetForm = async () => {
      if (!isEdit.value) {
        state.meetForm = {
          id: '',
          name: '',
          dates: [],
          themeImage: '',
          isInform: 0,
          introduce: '',
        };
      } else {
        const { item } = props;
        const themeImage = filterThumb(item.themeImage, { width: 128, height: 128 } as any) as string;
        const dates = [item.startTime, item.endTime];
        state.meetForm = {
          ...state.meetForm, ...item, dates, themeImage: getRelativePathFromUrl(item.themeImage),
        };

        // 通过urlToBlob的方式设置图片
        if (themeImage) {
          state.themeImage = await urlToBlob(themeImage) as File;
        }
      }
      state.originMeetForm = JSON.parse(JSON.stringify(state.meetForm)); // 保存原始对象值,用于对比前后修改的值
      state.diffInfo = {};
      form.value?.clearValidate();
    };
    onMounted(async () => {
      initMeetForm();
    });


    const themeImageChange = async (image: File | undefined, imgUrl: string) => {
      state.themeImage = image;
      if (!image) {
        state.meetForm.themeImage = '';
      }
      if (state.themeImage) {
        try {
          state.themeImageLoading = true;
          const imgKey = await uploadToOSS(state.themeImage!, 'MEETING');
          state.meetForm.themeImage = imgKey;
        } finally {
          state.themeImageLoading = false;
        }
      }
    };
    const logoImageChange = (image: File) => {

    };


    const cancel = () => {
      emit('cancel');
    };
    const commit = async () => {
      if (!form.value) return;
      await validateForm(form.value);
      try {
        const {
          id, name, dates, themeImage, introduce, isInform,
        } = state.meetForm;
        // state.diffInfo = getItemDiff();
        let editParams = {};
        if (isEdit.value) {
          const { diff } = state.diffInfo;
          const diffParams = Object.entries(diff!).reduce((obj, [key, item]) => {
          // eslint-disable-next-line prefer-destructuring
            obj[key] = item[0];
            return obj;
          }, {} as {[any:string]:any});


          const mailStatus = await root.$injectDialog({
            is: NoticeDialog,
          });
          const timeParams = diffParams.dates ? {
            startTime: dates[0] || 0,
            endTime: dates[1] || 0,
          } : {};
          editParams = {
            id,
            ...diffParams,
            ...timeParams,
            mailStatus,
          };
          if (diffParams.dates) delete diffParams.dates;
        }
        state.loading = true;
        const params = isEdit.value ? editParams : {
          id,
          name,
          themeImage,
          introduce,
          startTime: dates[0] || 0,
          endTime: dates[1] || 0,
          isInform,
        };
        const item = (await operateMeeting(params)).data || {};
        emit('confirm', { item, frontItem: state.meetForm, diffInfo: state.diffInfo });
      } finally {
        state.loading = false;
        state.diffInfo = {};
      }
    };
    async function handleClickAddMembers() {
      const addMembers = await root.$injectDialog({
        is: MemberAdd,
        attrs: {
          membersHandler: async function membersHandler(
            this: InstanceType<typeof MemberSelector> | InstanceType<typeof MemberSearcher>,
            memberList: (User & { enable: boolean } & { roleId?: string })[],
          ) {
            const isSearchResult = this.$options.name === 'MemberSearcher';
            // 区分账号类型，若为虚拟账号，需判断是否有绑定workspace或会议，有绑定就不可用；其他账号判断是否有绑定此会议，有则不可用
            if (isSearchResult) {
              if (!memberList.length) return memberList;
              const virtualAccountIds = memberList.filter(mem => +mem.roleId === 3).map(mem => mem.id);
              const otherAccountIds = difference(memberList.map(i => i.id), virtualAccountIds);
              if (otherAccountIds.length) {
                const { data: usersUsable } = await axios.post<Record<string, boolean>>('/meeting/member/exists', {
                  userIds: otherAccountIds,
                  cooperationid: props.item.cooperationId,
                });
                Object.entries(usersUsable).forEach(([uid, exist]) => { memberList.find(({ id }) => id === uid)!.enable = !exist; });
              }
              if (virtualAccountIds.length) {
                virtualAccountIds.forEach((id) => { memberList.find(i => i.id === id)!.enable = true; });
                const isVirtualAccountInWorkspace = (await axios.post('/authcenter/user/check/batch', {
                  userIds: virtualAccountIds,
                })).data as Record<string, boolean>;
                const isVirtualAccountInMeeting = (await axios.post('/meeting/member/virtual/exists', {
                  userIds: virtualAccountIds,
                })).data as Record<string, boolean>;
                const shouldDisabledList = Object.entries(isVirtualAccountInWorkspace).filter(([_, value]) => value).concat(
                  Object.entries(isVirtualAccountInMeeting).filter(([_, value]) => value),
                );
                shouldDisabledList.forEach(([id]) => { memberList.find(i => i.id === id)!.enable = false; });
              }

              return memberList;
            }
            if (!memberList.length) return memberList;
            const self = this as InstanceType<typeof MemberSelector>;
            const isVirtualAccount = self.tabValue === self.tabValues[2];
            if (isVirtualAccount) {
              memberList.forEach(i => { i.enable = true; });
              const isVirtualAccountInWorkspace = (await axios.post('/authcenter/user/check/batch', {
                userIds: memberList.map(member => member.id),
              })).data as Record<string, boolean>;
              const isVirtualAccountInMeeting = (await axios.post('/meeting/member/virtual/exists', {
                userIds: memberList.map(member => member.id),
              })).data as Record<string, boolean>;
              const shouldDisabledList = Object.entries(isVirtualAccountInWorkspace).filter(([_, value]) => value).concat(
                Object.entries(isVirtualAccountInMeeting).filter(([_, value]) => value),
              );
              shouldDisabledList.forEach(([id]) => { memberList.find(i => i.id === id)!.enable = false; });
              return memberList;
            }
            const { data: usersUsable } = await axios.post<Record<string, boolean>>('/meeting/member/exists', {
              userIds: memberList.map(member => member.id),
              cooperationid: props.item.cooperationId,
            });
            Object.entries(usersUsable).forEach(([uid, exist]) => { memberList.find(({ id }) => id === uid)!.enable = !exist; });
            return memberList;
          },
          tabLabels: [
            i18n.t('workspace.internal_contact'),
            i18n.t('workspace.external_contack'),
            i18n.t('virtual-meetup.others.meetup_access_pass'),
          ],
        },
      }, {
        key: props.item.cooperationId,
      }) as User[];
      const userIds = addMembers.map(u => u.id);
      await axios.post('/meeting/member/add', { userIds, cooperationId: props.item.cooperationId });
      root.$message.success(i18n.t('workspace.add_members_success_tip'));
      meetUsersRef.value?.reloadUsers();
    }
    async function handleClickInvitePeoples() {
      await root.$injectDialog({
        is: MemberInvitation,
        props: {
          meetingId: props.item.id,
          cooperationId: props.item.cooperationId,
          meetingName: state.meetForm.name,
        },
      });
    }
    async function handleClickCreateWorkspacePass() {
      await root.$injectDialog({
        is: MeetupAccessPassGenerator,
        props: {
          cooperationId: props.item.cooperationId,
        },
      });
      meetUsersRef.value?.reloadUsers();
    }
    return {
      ...toRefs(state),
      form,
      themeImageChange,
      logoImageChange,
      cancel,
      commit,
      operateMeeting,
      isEdit,
      hasDiffInfo,
      meetUsersRef,

      handleClickAddMembers,
      handleClickInvitePeoples,
      handleClickCreateWorkspacePass,
    };
  },
});
