
import axios from '@/utils/axios';
import useLoader from '@4dst-saas/public-utils/dist/composables/use-loader';
import { computed, defineComponent, ref } from '@vue/composition-api';
import i18n from 'i18next';
import { writeText as copyText2Clipboard } from '@/utils/clipboard';

enum InviteMode {
  SINGLE = 'single',
  MULTIPLE = 'multiple'
}

export default defineComponent({
  dialogify: {
    props: {
      title: i18n.t('workspace.invite_people'),
    },
  },
  props: {
    meetingId: {
      type: String,
      required: true,
    },
    cooperationId: {
      type: String,
      required: true,
    },
  },
  render() {
    const isSingleMode = this.mode === InviteMode.SINGLE;
    return <section class="container flex-column">
      {
        isSingleMode
          ? <div class="single-invitation flex-column">
            <div class="invitation-title">{this.$t('workspace.email_invitation')}</div>
            <el-form label-width="80px" size='small' label-position="left">
              <el-form-item label={this.$t('workspace.email')}>
                <el-input placeholder={this.$t('workspace.email_input_placeholder')}
                  onfocus={() => { this.showRemarks = true; }} vModel={this.singleEmail}></el-input>
              </el-form-item>
              {
                this.showRemarks
                  ? <el-form-item label={this.$t('workspace.remarks')}>
                    <el-input type="textarea" vModel={this.remarks}
                      placeholder={this.$t('workspace.remarks_tip')}></el-input>
                  </el-form-item>
                  : null
              }
            </el-form>
            <section class='flex-justify-center flex-column'
            >

              <div class="invitation-title">{this.$t('workspace.link_invitation')}</div>
              {
                this.generatedInvitationLink
                  ? <div class='link-invitation-container'
                    onclick={() => this.copyLink(this.generatedInvitationLink!)}>
                    <strong style="margin-left: 10px;">
                      <i class="iconfont icon-link link-icon"></i>
                    </strong>
                    <span>
                      {this.$t('workspace.copy_invitation_link')}
                    </span>
                  </div>
                  : <div vLoading={true} style={{ height: '100px', width: '100%' }}></div>
              }
            </section>
          </div>
          : <div class="multiple-invitation flex-column">
            <el-form label-width="80px" size='small' label-position="left">
              <el-form-item label={this.$t('workspace.email')}>
                <el-input type='textarea' placeholder={this.$t('workspace.multiple_invitation_placeholder')}
                  vModel={this.multipleEmail} rows='5'
                  onfocus={() => { this.showRemarks = true; }}></el-input>
              </el-form-item>
              {
                this.showRemarks
                  ? <el-form-item label={this.$t('workspace.remarks')}>
                    <el-input type="textarea" vModel={this.remarks}
                      placeholder={this.$t('workspace.remarks_tip')}></el-input>
                  </el-form-item>
                  : null
              }
            </el-form>
          </div>
      }
      <footer class="footer flex-space-between">
        {
          isSingleMode
            ? <div class="to-multiple-invitation" onclick={() => { this.mode = InviteMode.MULTIPLE; }}>
              <i class="iconfont icon-bulk-invitation-o"></i>
              <span>
                {isSingleMode ? this.$t('workspace.invite_multiple_partners')
                  : this.$t('workspace.create_multiple_partners')}
              </span>
            </div>
            : <div class="to-single-invitation" onclick={() => { this.mode = InviteMode.SINGLE; }}>
              <i class="iconfont icon-single-invitation-o"></i>
              <span>
                {isSingleMode ? this.$t('workspace.invite_a_partner')
                  : this.$t('workspace.create_a_partner')}
              </span>
            </div>
        }
        <div class='button-group'>
          <el-button onclick={() => this.$emit('cancel')}>{i18n.t('public.dialog.cancel')}</el-button>
          <el-button disabled={!this.isConfirmAvailable} type='primary'
            onclick={this.handleClickConfirm}>{i18n.t('public.dialog.confirm')}</el-button>
        </div>
      </footer>
    </section>;
  },
  setup(props, { root, emit }) {
    const mode = ref(InviteMode.SINGLE);
    const singleEmail = ref('');
    const remarks = ref('');
    const singleEmailValid = computed(() => /\w+@\w+.\w+/.test(singleEmail.value));
    const multipleEmail = ref('');
    const splitMultiEmail = computed(() => multipleEmail.value.split(/\s+/));
    // eslint-disable-next-line max-len
    const multipleEmailValid = computed(() => multipleEmail.value && splitMultiEmail.value.length === splitMultiEmail.value.map(e => /\w+@\w+.\w+/.test(e) || e === '').filter(e => e).length);
    const isLinkInvitationClick = ref(false);

    // eslint-disable-next-line
    const generatedInvitationLink = useLoader(async ({ meetingId }) => (await axios.get<string>(`/meeting/member/invite/url?meetingId=${meetingId}`)).data, props);

    const isConfirmAvailable = computed(() => {
      if (mode.value === InviteMode.SINGLE) {
        return isLinkInvitationClick.value || singleEmailValid.value;
      }
      return multipleEmailValid.value;
    });
    async function handleClickConfirm() {
      if (isLinkInvitationClick.value) emit('cancel');
      const { meetingId, cooperationId } = props;
      const data = {
        mails: mode.value === InviteMode.SINGLE ? [singleEmail.value] : splitMultiEmail.value.filter(i => i),
        meetingId,
        cooperationId,
        remark: remarks.value,
      };
      await axios.post('/meeting/member/invite/mail', data);
      root.$message.success(i18n.t('public.form.mail_suc'));
      emit('confirm');
      // else if (mode.value === InviteMode.SINGLE) axios.post;
      // else axios.post;
    }

    async function copyLink(link: string) {
      if (await copyText2Clipboard(link)) {
        root.$message.success(i18n.t('profile.account.copy_success'));
      }
    }

    return {
      mode,
      generatedInvitationLink,
      singleEmail,
      remarks,
      multipleEmail,
      isLinkInvitationClick,
      isConfirmAvailable,
      showRemarks: ref(false),

      splitMultiEmail,

      handleClickConfirm,
      copyLink,
    };
  },
});
