












































import { i18n } from '@/i18n';
import {
  defineComponent, reactive, computed, ref, toRefs,
} from '@vue/composition-api';
import OperateMeeting from './components/OperateMeeting.vue';
import { Meet, MeetList, Tabs } from './index.vue';
import MemberManagement from './components/MemberManagement.vue';

export default defineComponent({
  name: 'up-list__header',
  components: {},
  props: {
    tabs: {
      type: Array as () => Tabs,
      default: () => [],
    },
  },
  setup(props, { refs, emit, root }) {
    // async function loadMeeting() {
    //   let meeting: Meeting;
    //   while (true) {
    //     try {
    //       axios.get('id');
    //     } catch (e) {
    //       if (e /* 加载不到 */) {

    //       } else {
    //         throw e;
    //       }
    //     }
    //     if (meeting) {
    //       break;
    //     }
    //     await delay(5000);
    //   }
    //   return meeting;
    // }
    const state = reactive({
      searchValue: '',
      currentTab: '0',
    });
    const search = () => {
      refs?.searchInput?.blur();
      emit('search', state.searchValue);
    };
    const createMeeting = async () => {
      const { item } = await root.$injectDialog({
        is: OperateMeeting,
        props: {
          item: {},
        },
      }, {
        props: {
          title: i18n.t('virtual-meetup.list.create'),
        },
      });
      await root.$injectDialog({
        is: MemberManagement,
        props: {
          cooperationId: item.cooperationId,
          meetingId: item.id,
          item,
        },
      }, {
        props: {
          title: item?.name,
        },
      });
      emit('create-meeting', { item });
    };
    const tabClick = () => {
      emit('tab-click', state.currentTab);
    };
    return {
      ...toRefs(state),
      createMeeting,
      search,
      tabClick,
    };
  },
});
