























import {
  defineComponent, reactive, computed, ref, toRefs,
} from '@vue/composition-api';
import axios from '@/utils/axios';
import { getContextUser } from '@/loaders/context';
import delay from '_/delay';
import { getRelativePathFromUrl } from '@/utils/normalize-component';
import { i18n } from '@/i18n';
import { Meeting } from '../typings';
import Header from './Header.vue';
import List from './components/List.vue';
import Empty from './components/Empty.vue';
import SearchEmpty from './components/SearchEmpty.vue';
import { MeetForm } from './components/OperateMeeting.vue';

type FetchListParams = { status?:string, condition:string, openPage?:boolean};

export type Meet = {
  id:string,
  name:string,
  startTime:number,
  endTime:number,
  themeImage:string
  status:number,
  creator: string,
  cooperationId?:string,
  isOpenSetting?:boolean
};

export type MeetList = Meet[];

export type Tab = {
  label:string,
  name:string,
  total:number
};
export type Tabs = Tab[];


export const fetchMeetList = async (params:FetchListParams) => {
  const res = (await axios({
    url: '/meeting/meeting/search',
    method: 'post',
    data: { ...params, type: [1] },
  }))?.data || {};
  return res;
};


export const fetchTotals = async (tabs:Tabs) => {
  const totalsObj:{[any:string]:number} = (await axios({
    url: '/meeting/meeting/countSearch',
    method: 'post',
    data: { type: [1] },
  }))?.data || {};
  const allTotal = Object.values(totalsObj).reduce((total, cur) => total + cur);
  totalsObj[0] = allTotal || 0;

  tabs.forEach(async tab => {
    tab.total = totalsObj[tab.name] || 0;
  });
  return tabs;
};

export default defineComponent({
  name: 'up-meeting__list',
  components: {
    Header, List, Empty, SearchEmpty,
  },
  props: {},
  setup(props, { emit, root }) {
    const state = reactive({
      currentTab: '0',
      meetList: [] as MeetList,
      searchValue: '',
      tabs: [{
        label: i18n.t('virtual-meetup.list.all'),
        name: '0',
        total: 0,
      }, {
        label: i18n.t('virtual-meetup.list.pre'),
        name: '1',
        total: 0,
      }, {
        label: i18n.t('virtual-meetup.list.ing'),
        name: '2',
        total: 0,
      }, {
        label: i18n.t('virtual-meetup.list.end'),
        name: '3',
        total: 0,
      }],
      loading: false,
      loopIndex: 0,
    });
    const user = computed(() => getContextUser());
    const userRoleId = computed(() => user.value?.roleId);
    const getFetchMeetList = async () => {
      return (await fetchMeetList({
        status: state.currentTab === '0' ? '' : state.currentTab,
        condition: state.searchValue,
      })).records || [];
    };
    const load = async () => {
      try {
        state.loading = true;
        state.meetList = await getFetchMeetList();
      } finally {
        state.loading = false;
      }
    };
    const loopReset = () => {
      state.loading = false;
      state.loopIndex = 0;
    };
    const loopFetch = async (isLoopFn:(meetList:MeetList)=>boolean) => {
      try {
        state.loading = true;
        state.loopIndex++;
        await delay(500);
        const meetList:MeetList = await getFetchMeetList();

        if (isLoopFn(meetList) && state.loopIndex < 10) {
          await loopFetch(isLoopFn);
        } else {
          if (state.loopIndex >= 10) {
            window.location.reload();
            return;
          }
          state.meetList = meetList;
          fetchTotals(state.tabs);
          loopReset();
        }
      } catch {
        loopReset();
      }
    };
    const search = (searchValue:string) => {
      state.searchValue = searchValue;
      load();
    };
    const createMeeting = ({ item }:{item:Meet}) => {
      loopFetch((meetList:MeetList) => meetList.every(apiItem => apiItem.id !== item.id));
    };
    const editMeeting = ({ diffInfo }:{diffInfo:any}) => {
      const { id, diff } = diffInfo;
      const firstKey = Object.keys(diff)[0] as Exclude<keyof Meet, 'id'> | 'dates';

      if (!firstKey) {
        // 编辑前后没有改变
        load();
        fetchTotals(state.tabs);
      } else {
        const [val, oldVal] = diff[firstKey];
        const isSame = (value:any, oldValue:any) => {
          if (typeof value === 'object') {
            return JSON.stringify(value) === JSON.stringify(oldValue);
          }
          return value === oldValue;
        };
        loopFetch((meetList:MeetList) => {
          const meet = meetList.find(item => item.id === id);
          if (!meet) return false;
          switch (firstKey) {
            case 'dates':
              return !(isSame(meet?.startTime, val[0]) && isSame(meet?.endTime, val[1]));
            case 'themeImage':
              return !isSame(getRelativePathFromUrl(meet.themeImage), val);
            default:
              return !isSame(meet[firstKey], val);
          }
        });
      }
    };
    const tabClick = (currentTab:string) => {
      state.currentTab = currentTab;
      load();
    };
    const quit = (item:Meet) => {
      loopFetch((meetList:MeetList) => meetList.some(apiItem => apiItem.id === item.id));
      // load();
    };

    const init = async () => {
      load();
      fetchTotals(state.tabs);
    };
    init();
    return {
      ...toRefs(state),
      search,
      createMeeting,
      editMeeting,
      tabClick,
      quit,
      userRoleId,
    };
  },
});
