


























































































import {
  defineComponent, reactive, computed, ref, toRefs, watch,
} from '@vue/composition-api';
import dayjs from 'dayjs';
import OverText from '@/components/OverText.vue';
import axios from '@/utils/axios';
import BaseImage from '@/components/base/Image';
import { getContextUser } from '@/loaders/context';
import openWindow from '@/utils/open-window';
import { i18n, defaultLang } from '@/i18n';
import isQt from '@/utils/is-qt';
import exec from '@/utils/exec';
import { Meet, MeetList } from '../index.vue';
import MeetingIcon from './MeetingIcon.vue';
import MeetingDetail from './MeetingDetail.vue';
import OperateMeeting from './OperateMeeting.vue';


const delMeeting = async (params:{meetingId:string}) => {
  const res = await axios({
    url: '/meeting/meeting/delete',
    params,
  });
  return res;
};

const quitMeeting = async (params:{cooperationId:string, userIds:string[]}) => {
  const res = await axios({
    url: '/meeting/member/remove',
    method: 'post',
    data: params,
  });
  return res;
};

export default defineComponent({
  name: 'meet-list',
  components: {
    MeetingIcon, OverText, BaseImage,
  },
  props: {
    data: {
      type: Array as ()=> MeetList,
      default: () => [],
    },
  },
  setup(props, { emit, root }) {
    const state = reactive({
      currentData: [] as MeetList,
    });
    const user = computed(() => getContextUser());
    const userId = computed(() => user.value?.id || '');
    const userRoleId = computed(() => user.value?.roleId);

    watch(() => props.data, () => {
      state.currentData = props.data.map(item => ({ ...item, isOpenSetting: false }));
    });
    const handleDates = (dates:[number, number]) => {
      if (!dates || !dates.length) return '';
      const formatDate = (date:number) => dayjs(date).format('MM/DD HH:mm');
      return `${formatDate(dates[0])} - ${formatDate(dates[1])}`;
    };
    const isMeetCreator = (creatorId:string) => {
      return creatorId === userId.value;
    };
    const openSetting = async (meet:Meet, _isMeetCreator:boolean) => {
      meet.isOpenSetting = false;
      const is = _isMeetCreator ? OperateMeeting : MeetingDetail;
      const title = _isMeetCreator ? i18n.t('virtual-meetup.list.edit') : i18n.t('virtual-meetup.list.detail');
      const { diffInfo, frontItem } = await root.$injectDialog({
        is,
        props: {
          item: meet,
          user: user.value,
        },
      }, {
        props: {
          title,
        },

      });
      emit('edit-meeting', { diffInfo, frontItem });
    };

    const quit = async (item:Meet, _isMeetCreator:boolean) => {
      const action = _isMeetCreator ? i18n.t('virtual-meetup.list.del') : i18n.t('virtual-meetup.list.quit');
      await root.$confirm(i18n.t('virtual-meetup.list.confirm_tip', [action, item.name]), action, {
        confirmButtonText: action,
        cancelButtonText: i18n.t('virtual-meetup.public.cancel'),
        dangerouslyUseHTMLString: true,
      });
      if (_isMeetCreator) {
        await delMeeting({ meetingId: item.id });
      } else {
        await quitMeeting({ cooperationId: item?.cooperationId || '', userIds: [userId.value] });
      }
      emit('quit', item);
    };
    const toMeetingHome = (item:Meet) => {
      const meetingId = item.id;
      if (isQt()) {
        exec('openMeetingRoom', meetingId);
      } else {
        openWindow({ path: '/up-meeting', query: { meetingId } });
      }
    };
    return {
      ...toRefs(state),
      handleDates,
      openSetting,
      quit,
      toMeetingHome,
      userId,
      userRoleId,
      isMeetCreator,
    };
  },
});
