




































































import {
  defineComponent, reactive, computed, ref, toRefs, watch,
} from '@vue/composition-api';
import BaseImage from '@/components/base/Image.vue';
import OverText from '@/components/OverText.vue';
import axios from '@/utils/axios';
import delay from '_/delay';
// import User from '@/models/User';
import Paging from '@/models/Paging';
import { Meet } from '../index.vue';
import ShowMore from './ShowMore.vue';


interface MeetUser {
  id: string,
  name: string,
  icon: string
}
type MeetUsers = MeetUser[];
const pageSize = 10;

export default defineComponent({
  name: 'meet-users',
  components: {
    BaseImage, OverText,
  },
  props: {
    cooperationId: {
      type: String,
      required: true,
    },
    meetingId: {
      type: String,
      default: '',
    },
    value: {
      type: Array as () => MeetUsers,
      default: () => [],
    },
    isReview: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { refs, emit, root }) {
    const state = reactive({
      meet: {} as Meet,
      meetUsers: [] as MeetUsers,
      pageNumber: 1,
    });
    watch(() => props.value, (value) => {
      state.meetUsers = value;
    });
    watch(() => state.meetUsers, (meetUsers) => {
      emit('input', meetUsers);
    });
    const addUser = (command: 'add' | 'invite' | 'virtual') => {
      console.log(command);
    };
    const delUser = async (user: MeetUser) => {
      state.meetUsers.splice(state.meetUsers.indexOf(user), 1);
      await axios.post('/meeting/member/remove', {
        userIds: [user.id],
        cooperationId: props.cooperationId,
      });
    };
    const fetchMeet = async () => {
      const meet = (await axios({
        url: '/meeting/meeting/info',
        params: { meetingId: props.meetingId },
      }))?.data;
      return meet;
    };
    const omittedUserCount = ref(0);
    const fetchMeetUsers = async (pageNumber = 1) => {
      const res = (await axios({
        url: '/meeting/member/page',
        params: { cooperationId: state.meet?.cooperationId, pageNumber, pageSize },
      }))?.data as Paging<{ userId: string }>;
      omittedUserCount.value = res.total! - res.current * pageSize;
      const userIds = res.records!.map(i => i.userId);
      const users = (await axios({
        method: 'post',
        url: 'authcenter/user/im/info',
        data: userIds,
      })).data as MeetUser[];
      return userIds.map(id => users.find(u => u.id === id)!);
    };
    const loadMore = async () => {
      state.meetUsers.push(...await fetchMeetUsers(++state.pageNumber));
    };
    async function reloadUsers() {
      state.pageNumber = 1;
      const res = await fetchMeetUsers() || [];
      state.meetUsers = res;
    }
    const init = async () => {
      state.meet = await fetchMeet();
      await reloadUsers();
    };
    init();
    return {
      ...toRefs(state),
      omittedUserCount,
      addUser,
      delUser,
      fetchMeetUsers,
      loadMore,
      reloadUsers,
    };
  },
});
