
import {
  computed, defineComponent, reactive, ref,
} from '@vue/composition-api';
import validPassword from '@/utils/validators/valid-password';
import Schema2Component from '@4dst-saas/form-utils/dist/components/jsonschema2form';
import { startOfDate } from '_/date';
import { getContextUser } from '@/loaders/context';
import { randomString } from '_/random-string';
import axios from '@/utils/axios';
import i18n from 'i18next';


export default defineComponent({
  dialogify: {
    props: {
      title: i18n.t('virtual-meetup.others.create_meetup_access_pass'),
    },
  },
  props: {
    cooperationId: {
      type: String,
      required: true,
    },
  },
  render() {
    const formPart = !this.batch
      ? <Schema2Component form={this.singleForm} ref='singleForm' jsonschema={this.jsonschema} form-item-validator={{
        validPassword,
        accountValidator: (value: string) => {
          const reg = /^[a-zA-Z_0-9]+$/g;
          if (reg.test(value)) {
            const hasNum = /\d/.test(value);
            const hasLetter = /[a-zA-Z]/.test(value);
            const hasUnderLine = /[_]/.test(value);
            const score = [hasNum, hasLetter, hasUnderLine].reduce(((total, v) => total + (v ? 1 : 0)), 0);
            if (score < 2) {
              throw new Error(this.$t('profile.account.account_validator_tip'));
            }
          } else {
            throw new Error(this.$t('profile.account.account_validator_tip'));
          }
        },
      }} size='small' onfocus={(e: { key: string }) => {
        if (e.key === 'mails') { this.jsonschema.find(i => i.prop === 'remarks')!.show = true; }
      }}></Schema2Component>
      : <el-form>
        <el-form-item label={this.$t('profile.account.recipient_email')}>
          <el-input type="textarea" vModel={this.multiForm.mails} onfocus={() => { this.multiShowRemarks = true; }}
            placeholder={this.$t('profile.account.multiple_invitation_placeholder')}></el-input>
        </el-form-item>
        {
          this.multiShowRemarks
            ? <el-form-item label={this.$t('profile.account.remarks')}>
              <el-input vModel={this.multiForm.remarks} type='textarea' placeholder={this.$t('profile.account.remarks_tip')}></el-input>
            </el-form-item>
            : null
        }
      </el-form>;
    return <section class="flex-column">
      {formPart}
      <el-divider></el-divider>
      <el-radio-group vModel={this.withMainAccount} class='flex-column radio-group'>
        <el-radio label={true}>
          {this.$t('profile.account.same_as_subs_plan')}</el-radio>
        <el-radio label={false}>{this.$t('profile.account.cus_acct_exp_date')}</el-radio>
        <el-date-picker size="small"
          vShow={!this.withMainAccount}
          vModel={this.dataRange}
          default-time={['00:00:00', '23:59:59']}
          picker-options={{
            disabledDate: this.disabledRangeDate,
          }}
          type="daterange"
          format="MM/dd/yyyy"
          class="data-picker"
          start-placeholder={this.$t('profile.account.start_date')}
          end-placeholder={this.$t('profile.account.end_date')}
          onchange={([startTime, endTime]: Date[]) => {
            this.startTime = startTime.getTime();
            this.endTime = endTime.getTime();
          }} />
      </el-radio-group>
      <footer class="footer flex-space-between">
        {
          !this.batch
            ? <div class="to-multiple-invitation" onclick={() => { this.batch = true; }}>
              <i class="iconfont icon-bulk-invitation-o"></i>
              <span>
                {this.batch ? this.$t('workspace.invite_multiple_partners')
                  : this.$t('workspace.create_multiple_partners')}
              </span>
            </div>
            : <div class="to-single-invitation" onclick={() => { this.batch = false; }}>
              <i class="iconfont icon-single-invitation-o"></i>
              <span>
                {this.batch ? this.$t('workspace.invite_a_partner')
                  : this.$t('workspace.create_a_partner')}
              </span>
            </div>
        }
        <div class='button-group'>
          <el-button onclick={() => this.$emit('cancel')}>{this.$t('public.dialog.cancel')}</el-button>
          <el-button disabled={this.$isActing(this.handleClickConfirm)} type='primary'
            onclick={() => this.$act(this.handleClickConfirm)}>{this.$t('public.dialog.confirm')}</el-button>
        </div>
      </footer>
    </section>;
  },
  // @ts-ignore
  setup(props, { refs, emit }) {
    const jsonschema = reactive([
      {
        required: true,
        type: 'string',
        title: i18n.t('virtual-meetup.others.meetup_access_login_id'),
        prop: 'account',
        validator: ['accountValidator'],
      },
      {
        required: true,
        type: 'string',
        title: i18n.t('profile.account.temporary_password'),
        prop: 'pass',
        'show-password': true,
        inputType: 'password',
        validator: ['validPassword'],
      },
      {
        type: 'string',
        title: i18n.t('profile.account.recipient_email'),
        prop: 'mails',
      },
      {
        type: 'string',
        title: i18n.t('profile.account.remarks'),
        prop: 'remarks',
        show: false,
      },
      {
        type: 'string',
        title: i18n.t('profile.account.company'),
        prop: 'companyName',
      },
    ]);
    const daysRemaining = computed(() => getContextUser()?.daysRemaining || 0);
    // 控制日期选中组件中，不可选择日期的范围
    function disabledRangeDate(time: number) {
      const todaytoday = +startOfDate(new Date());
      const end = todaytoday + (daysRemaining.value + 1) * 24 * 3600 * 1000 - 1;
      return time < todaytoday || time > end;
    }
    const batch = ref(false);
    const singleForm = reactive({
      account: randomString(6),
      pass: 'Hello123~',
    });
    const multiForm = reactive({
      mails: '',
      remarks: '',
    });
    const startTime = ref<number>();
    const endTime = ref<number>();
    const multiShowRemarks = ref(false);
    const withMainAccount = ref(true);
    async function handleClickConfirm() {
      const data = !batch.value
        ? {
          ...singleForm,
        } : {
          ...multiForm,
          mails: multiForm.mails.split(/\s+/),
        } as Record<string, unknown>;
      if (!withMainAccount.value) {
        data.startTime = startTime.value;
        data.endTime = endTime.value;
      }
      data.batch = batch.value;
      data.cooperationId = props.cooperationId;
      await axios.post('/meeting/member/add/virtual', data);
      emit('confirm');
    }
    return {
      jsonschema,
      batch,
      singleForm,
      multiForm,
      multiShowRemarks,
      withMainAccount,
      startTime,
      endTime,
      dataRange: ref([]),

      disabledRangeDate,
      handleClickConfirm,
    };
  },
});
