
import Paging from '@/models/Paging';
import axios from '@/utils/axios';
import callbackWhenLastElementShown from '@/utils/composables/use-callback-when-last-element-shown';
import useLoader from '@4dst-saas/public-utils/dist/composables/use-loader';
import { defineComponent, ref } from '@vue/composition-api';
import BaseUser from '@/components/base/User.vue';
import MemberAddVue from '@/views/file-list/components/workspace/MemberAdd.vue';
import MemberSelectorVue from '@/views/file-list/components/workspace/MemberSelector.vue';
import MemberSearcherVue from '@/views/file-list/components/workspace/MemberSearcher.vue';
import User from '@/models/User';
import difference from '@4dst-saas/public-utils/dist/difference';
import i18n from 'i18next';
import OverText from '@/components/OverText.vue';
import MemberInvitationVue from './MemberInvitation.vue';

const pageSize = 10;
interface MeetUser {
  id: string,
  name: string,
  icon: string,
  account: string,
  mail: string,
}

export default defineComponent({
  dialogify: {
    closeWith: () => [],
  },
  props: {
    cooperationId: {
      type: String,
      required: true,
    },
    meetingId: {
      type: String,
      required: true,
    },
  },
  render() {
    return <section class='container flex-column'>
      <div class='flex-space-between user-info-title'>
        <div>{this.$t('workspace.email_or_id')}</div>
        <div>{this.$t('workspace.username')}</div>
      </div>
      <ul class='flex-column users-container scrollable-y' vLoading={!this.meetUsers}>
        {
          this.meetUsers?.map(user => <li class='user-item flex-space-between flex-vertical-center'>
            <div class='user-profile'>
              <BaseUser user={user} size="small">
                <OverText style={{ maxWidth: '190px' }}>{user.account || user.mail}</OverText>
              </BaseUser>
            </div>
            <OverText style={{ maxWidth: '50%' }}>{user.name}</OverText>
          </li>)
        }
      </ul>
      <footer class="footer flex-space-between">
        <el-tooltip content={this.$t('workspace.share_and_invite_partners')} placement="top">
          <el-button
            type="text"
            class="invite-members"
            onclick={this.handleClickInvitePeoples}
          >
            <i class="iconfont icon-invitation-o"></i>
            {this.$t('workspace.share_and_invite_permission')}
          </el-button>
        </el-tooltip>

        <el-button
          type="primary"
          class="add-members"
          onclick={this.handleClickAddMembers}>
          {this.$t('workspace.add_members')}
        </el-button>
      </footer>
    </section>;
  },
  setup(props, { root }) {
    const currentPage = ref(1);
    const totalPage = ref(1);
    // eslint-disable-next-line @typescript-eslint/no-shadow
    const fetchMeetUsers = async (pageNumber = 1) => {
      const res = (await axios({
        url: '/meeting/member/page',
        params: { cooperationId: props.cooperationId, pageNumber, pageSize },
      }))?.data as Paging<{ userId: string }>;
      totalPage.value = res.pages!;
      const userIds = res.records!.map(i => i.userId);
      const users = (await axios({
        method: 'post',
        url: 'authcenter/user/im/info',
        data: userIds,
      })).data as MeetUser[];
      return userIds.map(id => users.find(u => u.id === id)!);
    };
    const meetUsers = useLoader(async () => {
      const res = await fetchMeetUsers();
      return res;
    }, {});
    const loadMore = async () => {
      if (currentPage.value < totalPage.value) {
        meetUsers.value!.push(...await fetchMeetUsers(++currentPage.value));
      }
    };
    async function reload() {
      currentPage.value = 1;
      meetUsers.value = await fetchMeetUsers();
    }
    callbackWhenLastElementShown(meetUsers, loadMore, '.user-item');
    async function handleClickAddMembers() {
      const addMembers = await root.$injectDialog({
        is: MemberAddVue,
        attrs: {
          membersHandler: async function membersHandler(
            this: InstanceType<typeof MemberSelectorVue> | InstanceType<typeof MemberSearcherVue>,
            memberList: (User & { enable: boolean } & { roleId?: string })[],
          ) {
            if (!memberList.length) return memberList;
            const isSearchResult = this.$options.name === 'MemberSearcher';
            // 区分账号类型，若为虚拟账号，需判断是否有绑定workspace或会议，有绑定就不可用；其他账号判断是否有绑定此会议，有则不可用
            if (isSearchResult) {
              const virtualAccountIds = memberList.filter(mem => +mem.roleId === 3).map(mem => mem.id);
              const otherAccountIds = difference(memberList.map(i => i.id), virtualAccountIds);
              if (otherAccountIds.length) {
                const { data: usersUsable } = await axios.post<Record<string, boolean>>('/meeting/member/exists', {
                  userIds: otherAccountIds,
                  cooperationid: props.cooperationId,
                });
                Object.entries(usersUsable).forEach(([uid, exist]) => { memberList.find(({ id }) => id === uid)!.enable = !exist; });
              }
              if (virtualAccountIds.length) {
                virtualAccountIds.forEach((id) => { memberList.find(i => i.id === id)!.enable = true; });
                const isVirtualAccountInWorkspace = (await axios.post('/authcenter/user/check/batch', {
                  userIds: virtualAccountIds,
                })).data as Record<string, boolean>;
                const isVirtualAccountInMeeting = (await axios.post('/meeting/member/virtual/exists', {
                  userIds: virtualAccountIds,
                })).data as Record<string, boolean>;
                const shouldDisabledList = Object.entries(isVirtualAccountInWorkspace).filter(([_, value]) => value).concat(
                  Object.entries(isVirtualAccountInMeeting).filter(([_, value]) => value),
                );
                shouldDisabledList.forEach(([id]) => { memberList.find(i => i.id === id)!.enable = false; });
              }
              return memberList;
            }
            const self = this as InstanceType<typeof MemberSelectorVue>;
            const isVirtualAccount = self.tabValue === self.tabValues[2];
            if (isVirtualAccount) {
              memberList.forEach(i => { i.enable = true; });
              const isVirtualAccountInWorkspace = (await axios.post('/authcenter/user/check/batch', {
                userIds: memberList.map(member => member.id),
              })).data as Record<string, boolean>;
              const isVirtualAccountInMeeting = (await axios.post('/meeting/member/virtual/exists', {
                userIds: memberList.map(member => member.id),
              })).data as Record<string, boolean>;
              const shouldDisabledList = Object.entries(isVirtualAccountInWorkspace).filter(([_, value]) => value).concat(
                Object.entries(isVirtualAccountInMeeting).filter(([_, value]) => value),
              );
              shouldDisabledList.forEach(([id]) => { memberList.find(i => i.id === id)!.enable = false; });
              return memberList;
            }
            const { data: usersUsable } = await axios.post<Record<string, boolean>>('/meeting/member/exists', {
              userIds: memberList.map(member => member.id),
              cooperationid: props.cooperationId,
            });
            Object.entries(usersUsable).forEach(([uid, exist]) => { memberList.find(({ id }) => id === uid)!.enable = !exist; });
            return memberList;
          },
          tabLabels: [
            i18n.t('workspace.internal_contact'),
            i18n.t('workspace.external_contack'),
            i18n.t('virtual-meetup.others.meetup_access_pass'),
          ],
        },
      }, {
        key: props.cooperationId,
      }) as User[];
      const userIds = addMembers.map(u => u.id);
      await axios.post('/meeting/member/add', { userIds, cooperationId: props.cooperationId });
      root.$message.success(i18n.t('workspace.add_members_success_tip'));
      reload();
    }
    async function handleClickInvitePeoples() {
      await root.$injectDialog({
        is: MemberInvitationVue,
        props: {
          meetingId: props.meetingId,
          cooperationId: props.cooperationId,
        },
      });
    }
    return {
      meetUsers,
      loadMore,
      reload,

      handleClickAddMembers,
      handleClickInvitePeoples,
    };
  },
});
